import{AddressTypes,base58,bech32,getAddressType,getPubKeyBlake2b224Hash,getShelleyAddressNetworkId,isValidBootstrapAddress,isValidShelleyAddress,packBaseAddress,packRewardAddress}from'cardano-crypto.js';import{EAdaNetworkId}from"../types";import{HARDENED_THRESHOLD}from"./constants";export var encodeAddress=function(address){var addressType=getAddressType(address);if(addressType===AddressTypes.BOOTSTRAP){return base58.encode(address);}var addressPrefixes={[AddressTypes.BASE]:'addr',[AddressTypes.POINTER]:'addr',[AddressTypes.ENTERPRISE]:'addr',[AddressTypes.REWARD]:'stake'};var isTestnet=getShelleyAddressNetworkId(address)===EAdaNetworkId.TESTNET_OR_PREPROD;var addressPrefix=`${addressPrefixes[addressType]}${isTestnet?'_test':''}`;return bech32.encode(addressPrefix,address);};export var xpub2pub=function(xpub){return xpub.slice(0,32);};export var xpub2ChainCode=function(xpub){return xpub.slice(32,64);};var xpub2blake2b224Hash=function(xpub){return getPubKeyBlake2b224Hash(xpub2pub(xpub));};export var isShelleyPath=function(path){return path[0]-HARDENED_THRESHOLD===1852;};export var stakingAddressFromXpub=function(stakeXpub,networkId){var addrBuffer=packRewardAddress(xpub2blake2b224Hash(stakeXpub),networkId);return encodeAddress(addrBuffer);};export var baseAddressFromXpub=function(spendXpub,stakeXpub,networkId){var addrBuffer=packBaseAddress(xpub2blake2b224Hash(spendXpub),xpub2blake2b224Hash(stakeXpub),networkId);return encodeAddress(addrBuffer);};export var validShelleyAddress=function(address){return(address.startsWith('addr1')||address.startsWith('addr_test1'))&&isValidShelleyAddress(address);};export var validBootstrapAddress=function(address){return(address.startsWith('Ae2')||address.startsWith('DdzFF'))&&isValidBootstrapAddress(address);};